export default {
	data() {
		return {
			isComputer:false,
			isSound:false,
			isProjector:false,
			isWater:false,
			name:'',
			desciption:'',
			user:'',
			phone:'',
			note:'',
			time:'',
			roomID:'',
			facility:'',
			text:''
		}
	},
	onLoad:function(option){
		console.log(option)
		this.id = option.id
		this.name = option.meetingName
		this.desciption = option.content
		this.user = option.name
		this.note = option.remark
		this.roomID = option.RoomId
		this.phone = option.phone
		var facility = option.facility
		let facilityArr = facility.split(',');
		var i;
		for(i=0;i<facilityArr.length;i++){
			if(facilityArr[i] == '电脑'){
				this.isComputer = true
				console.log("显示电脑")
			}
			if (facilityArr[i] == '音响'){
				this.isSound = true
				console.log("显示音响")
			}
			if (facilityArr[i] == '投影仪'){
				this.isProjector = true
				console.log("显示投影仪")
			}
			if (facilityArr[i] == '饮水机'){
				this.isWater = true
				console.log("显示饮水机")
			}
		}
		console.log(this.isComputer);
		this.time = option.startTime +'~'+option.endTime
		
	},
	methods: {
		
	}
}
